import React, { useEffect, useState } from "react";
import "../Styles/AdminFeatures.css";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const AdminFeatures = () => {
  const navigate = useNavigate();
  const [user, setUser] = useState(null);
  const [features, setFeatures] = useState([]);
  const [loading, setLoading] = useState(true);
  const [editingFeature, setEditingFeature] = useState(null);
  const [newFeature, setNewFeature] = useState({ title: "", description: "", icon: "" });

  const token = localStorage.getItem("token");
  const API = process.env.REACT_APP_BACKEND_URL;

  useEffect(() => {
    const fetchAdminAndFeatures = async () => {
      try {
        if (!token) {
          navigate("/login");
          return;
        }
        const me = await axios.get(`${API}/api/auth/me`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        if (me.data.user.role !== "admin") {
          alert("Access denied  Admins only");
          navigate("/");
          return;
        }

        setUser(me.data.user);
        const res = await axios.get(`${API}/api/features`, {
          headers: { Authorization: `Bearer ${token}` },
        });
        setFeatures(res.data.rows || []);
      } catch (err) {
        console.error("Failed to load admin/features:", err);
        localStorage.removeItem("token");
        navigate("/login");
      } finally {
        setLoading(false);
      }
    };

    fetchAdminAndFeatures();
  }, [navigate, token, API]);

  const handleEditChange = (e) => {
    setEditingFeature({ ...editingFeature, [e.target.name]: e.target.value });
  };

  const saveEdit = async () => {
    if (!editingFeature.title.trim() || !editingFeature.description.trim()) {
      return alert("Title & Description cannot be empty.");
    }

    try {
      const res = await axios.put(
        `${API}/api/features/${editingFeature.id}`,
        editingFeature,
        { headers: { Authorization: `Bearer ${token}` } }
      );

      if (res.data.success) {
        setFeatures((prev) =>
          prev.map((f) => (f.id === editingFeature.id ? editingFeature : f))
        );
        setEditingFeature(null);
        alert("Feature updated successfully!");
      }
    } catch (err) {
      console.error(err);
      alert("Error updating feature.");
    }
  };

  const deleteFeature = async (id) => {
    if (!window.confirm("Are you sure you want to delete this feature?")) return;

    try {
      const res = await axios.delete(`${API}/api/features/${id}`, {
        headers: { Authorization: `Bearer ${token}` },
      });

      if (res.data.success) {
        setFeatures((prev) => prev.filter((f) => f.id !== id));
      }
    } catch (err) {
      console.error(err);
      alert("Error deleting feature.");
    }
  };

  const addFeature = async () => {
    if (!newFeature.title.trim() || !newFeature.description.trim()) {
      return alert("Title & Description cannot be empty.");
    }

    try {
      const res = await axios.post(`${API}/api/features`, newFeature, {
        headers: { Authorization: `Bearer ${token}` },
      });

      if (res.data.success) {
        setFeatures([...features, res.data.feature]);
        setNewFeature({ title: "", description: "", icon: "" });
        alert("Feature added successfully!");
      }
    } catch (err) {
      console.error(err);
      alert("Error adding feature.");
    }
  };

  if (loading) return <div className="admin-features-container">Loading...</div>;

  return (
    <div className="admin-features-container">
      <h1>Edit Features</h1>

      <div className="features-list">
        {features.map((feature) => (
          <div key={feature.id} className="feature-card">
            {editingFeature?.id === feature.id ? (
              <>
                <input
                  type="text"
                  name="icon"
                  value={editingFeature.icon}
                  onChange={handleEditChange}
                  placeholder="Feature Icon"
                />
                <input
                  type="text"
                  name="title"
                  value={editingFeature.title}
                  onChange={handleEditChange}
                  placeholder="Feature Title"
                />
                <textarea
                  name="description"
                  value={editingFeature.description}
                  onChange={handleEditChange}
                  placeholder="Feature Description"
                />
                <button className="save-btn" onClick={saveEdit}>Save</button>
                <button className="cancel-btn" onClick={() => setEditingFeature(null)}>Cancel</button>
              </>
            ) : (
              <>
                <h3>{feature.title}</h3>
                <p>{feature.description}</p>
                <p>Icon: {feature.icon}</p>
                <div className="actions">
                  <button className="edit-btn" onClick={() => setEditingFeature(feature)}>Edit</button>
                  <button className="delete-btn" onClick={() => deleteFeature(feature.id)}>Delete</button>
                </div>
              </>
            )}
          </div>
        ))}
      </div>

      <h2>Add New Feature</h2>
      <div className="add-feature-form">
        <input
          type="text"
          placeholder="Feature Icon"
          value={newFeature.icon}
          onChange={(e) => setNewFeature({ ...newFeature, icon: e.target.value })}
        />
        <input
          type="text"
          placeholder="Feature Title"
          value={newFeature.title}
          onChange={(e) => setNewFeature({ ...newFeature, title: e.target.value })}
        />
        <textarea
          placeholder="Feature Description"
          value={newFeature.description}
          onChange={(e) => setNewFeature({ ...newFeature, description: e.target.value })}
        />
        <button className="add-btn" onClick={addFeature}>Add Feature</button>
      </div>

      <button className="back-btn" onClick={() => navigate("/admin")}>Back to Admin Panel</button>
      <div className="admin-edit-decoration"></div>
    </div>
  );
};

export default AdminFeatures;
