import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "../Styles/AdminChoice.css";

const AdminChoice = () => {
  const [user, setUser] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    const fetchUser = async () => {
      const token = localStorage.getItem("token");
      if (!token) {
        navigate("/login");
        return;
      }

      try {
        const res = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/auth/me`,
          {
            headers: { Authorization: `Bearer ${token}` },
          }
        );

        const { user } = res.data;
        if (user.role !== "admin") {
          alert("Access denied  Admins only");
          navigate("/");
          return;
        }

        setUser(user);
      } catch (err) {
        console.error("Failed to fetch user:", err);
        localStorage.removeItem("token");
        navigate("/login");
      }
    };

    fetchUser();
  }, [navigate]);

  if (!user) return <div className="adminx-loading">Loading Admin Panel...</div>;

  return (
    <div className="admin-Container">
      <h1>Admin Panel</h1>
      <div className="row">
        <button className="adminbtn1" onClick={() => navigate("/admin/hero")}>
          Edit Hero
        </button>
        <button className="adminbtn2" onClick={() => navigate("/admin/about")}>
          Edit About
        </button>
        <button className="adminbtn3" onClick={() => navigate("/admin/contact")}>
          Edit Contact-US
        </button>
        <button className="adminbtn4" onClick={() => navigate("/admin/users")}>
          Edit Users
        </button>
        <button className="adminbtn5" onClick={() => navigate("/admin/support")}>
          Support Messages
        </button>
        <button className="adminbtn1" onClick={() => navigate("/admin/carousel")}>
          Edit Carousel
        </button>
        <button className="adminbtn2" onClick={() => navigate("/admin/stats")}>
          Edit Stats
        </button>
        <button className="adminbtn3" onClick={() => navigate("/admin/features")}>
          Edit Features
        </button>
      </div>
      <div className="admin-decoration"></div>
    </div>
  );
};

export default AdminChoice;
