import React, { useEffect, useState } from "react";
import axios from "axios";
import "../Styles/AdminCarousel.css";
import { useNavigate } from "react-router-dom";

const AdminCarousel = () => {
  const navigate = useNavigate();
  const [slides, setSlides] = useState([]);
  const [loading, setLoading] = useState(true);
  const [editingSlide, setEditingSlide] = useState(null);
  const [formData, setFormData] = useState({ text: "", image: null });

  const API = process.env.REACT_APP_BACKEND_URL;
  const token = localStorage.getItem("token");

  useEffect(() => {
    const checkAdminAndFetchSlides = async () => {
      try {
        if (!token) {
          navigate("/");
          return;
        }

        // Verify admin
        const me = await axios.get(`${API}/api/auth/me`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        if (me.data.user.role !== "admin") {
          navigate("/");
          return;
        }

        // Fetch slides
        const res = await axios.get(`${API}/api/carousel`);
        setSlides(res.data.slides || []);
      } catch (err) {
        console.error("Error verifying admin or fetching slides:", err);
        navigate("/");
      } finally {
        setLoading(false);
      }
    };

    checkAdminAndFetchSlides();
  }, [API, navigate, token]);

  const handleChangeText = (e) => {
    setFormData((prev) => ({ ...prev, text: e.target.value }));
  };

  const handleChangeImage = (e) => {
    setFormData((prev) => ({ ...prev, image: e.target.files[0] }));
  };

  const handleEdit = (slide) => {
    setEditingSlide(slide.id);
    setFormData({ text: slide.text, image: null });
  };

  const handleSave = async () => {
    if (!formData.text) {
      alert("Slide text is required");
      return;
    }

    try {
      const payload = new FormData();
      payload.append("text", formData.text);
      if (formData.image) payload.append("image", formData.image);

      const config = { headers: { Authorization: `Bearer ${token}` } };

      if (editingSlide) {
        await axios.put(`${API}/api/carousel/${editingSlide}`, payload, config);
        setSlides((prev) =>
          prev.map((s) =>
            s.id === editingSlide ? { ...s, text: formData.text } : s
          )
        );
      } else {
        const res = await axios.post(`${API}/api/carousel`, payload, config);
        setSlides((prev) => [res.data.slide, ...prev]);
      }

      setEditingSlide(null);
      setFormData({ text: "", image: null });
    } catch (err) {
      console.error("Failed to save slide:", err);
      alert("Failed to save slide");
    }
  };

  const handleDelete = async (id) => {
    if (!window.confirm("Delete this slide?")) return;

    try {
      await axios.delete(`${API}/api/carousel/${id}`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      setSlides((prev) => prev.filter((s) => s.id !== id));
    } catch (err) {
      console.error("Failed to delete slide:", err);
      alert("Failed to delete slide");
    }
  };

  if (loading) return <div className="admin-carousel-container">Loading...</div>;

  return (
    <div className="bg-background">
      <div className="admin-carousel-container">
        <h1>Manage Carousel Slides</h1>

        <div className="carousel-form">
          <label>
            Slide Text:
            <input type="text" value={formData.text} onChange={handleChangeText} />
          </label>

          <label>
            Image:
            <input type="file" accept="image/*" onChange={handleChangeImage} />
          </label>

          <button className="save-btn" onClick={handleSave}>
            {editingSlide ? "Update Slide" : "Add Slide"}
          </button>
        </div>

        <div className="carousel-list">
          {slides.map((slide) => (
            <div key={slide.id} className="carousel-item">
              {slide.image && <img src={`${API}${slide.image}`} alt={slide.text} />}
              <p>{slide.text}</p>

              <div className="item-actions">
                <button className="edit-btn" onClick={() => handleEdit(slide)}>
                  Edit
                </button>
                <button className="delete-btn" onClick={() => handleDelete(slide.id)}>
                  Delete
                </button>
              </div>
            </div>
          ))}
        </div>

        <button className="back-btn" onClick={() => navigate("/admin")}>
          Back to Admin Panel
        </button>
      </div>
    </div>
  );
};

export default AdminCarousel;
