import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "../Styles/AdminSupport.css";

const AdminSupport = () => {
  const navigate = useNavigate();
  const [user, setUser] = useState(null);
  const [messages, setMessages] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [notification, setNotification] = useState(null);

  const API = process.env.REACT_APP_BACKEND_URL;
  const token = localStorage.getItem("token");

  const showNotification = (message, type = "success") => {
    setNotification({ message, type });
    setTimeout(() => setNotification(null), 3000);
  };

  useEffect(() => {
    const fetchAdminAndMessages = async () => {
      try {
        if (!token) {
          navigate("/login");
          return;
        }
        const me = await axios.get(`${API}/api/auth/me`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        if (me.data.user.role !== "admin") {
          alert("Access denied  Admins only");
          navigate("/");
          return;
        }

        setUser(me.data.user);
        const res = await axios.get(`${API}/api/contact/getmessages`, {
          headers: { Authorization: `Bearer ${token}` },
        });
        setMessages(res.data || []);
      } catch (err) {
        console.error("Failed to fetch messages:", err);
        localStorage.removeItem("token");
        navigate("/login");
      } finally {
        setLoading(false);
      }
    };

    fetchAdminAndMessages();
  }, [navigate, token, API]);

  const handleDelete = async (id) => {
    if (!window.confirm(`Are you sure you want to delete message ID ${id}?`)) return;

    try {
      await axios.delete(`${API}/api/contact/messages/${id}`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      setMessages((prev) => prev.filter((msg) => msg.id !== id));
      showNotification(`Message ID ${id} deleted successfully!`, "success");
    } catch (err) {
      console.error("Failed to delete message:", err);
      showNotification("Failed to delete the message.", "error");
    }
  };

  const toggleReadStatus = (id) => {
    setMessages((prev) =>
      prev.map((msg) => (msg.id === id ? { ...msg, is_read: !msg.is_read } : msg))
    );
  };

  if (loading) return <div className="admin-support-container">Loading messages...</div>;
  if (error) return <div className="admin-support-container error">{error}</div>;

  return (
    <div className="bg-background">
      <div className="admin-support-container">
        {notification && <div className={`notification ${notification.type}`}>{notification.message}</div>}

        <h1>Customer Support Messages ({messages.length})</h1>

        <div className="messages-list">
          {messages.length === 0 ? (
            <p className="no-messages">No contact messages found.</p>
          ) : (
            messages.map((msg) => (
              <div key={msg.id} className={`message-card ${msg.is_read ? "read" : "unread"}`}>
                <div className="message-header">
                  <span className="sender-name"><i className="fas fa-user"></i> Name: {msg.name}</span>
                  <span className="sender-email"><i className="fas fa-envelope"></i> Email: {msg.email}</span>
                </div>

                <p className="message-body">Message: {msg.message}</p>

                <div className="message-footer">
                  <span className="timestamp">Received: {new Date(msg.created_at).toLocaleString()}</span>
                  <div className="message-actions">
                    <button
                      className={`read-btn ${msg.is_read ? "mark-unread" : "mark-read"}`}
                      onClick={() => toggleReadStatus(msg.id)}
                    >
                      {msg.is_read ? "Mark Unread" : "Mark Read"}
                    </button>
                    <button className="delete-btn" onClick={() => handleDelete(msg.id)}>Delete</button>
                  </div>
                </div>
              </div>
            ))
          )}
        </div>

        <button className="back-btn" onClick={() => navigate("/admin")}>Back to Admin Panel</button>
      </div>
    </div>
  );
};

export default AdminSupport;
