import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "../Styles/AdminHero.css";

const AdminHero = () => {
  const navigate = useNavigate();
  const [user, setUser] = useState(null);
  const [hero, setHero] = useState({ title: "", subtitle: "", ctaPrimary: "", ctaSecondary: "" });
  const [formData, setFormData] = useState({ title: "", subtitle: "", ctaPrimary: "", ctaSecondary: "" });
  const [editing, setEditing] = useState(false);
  const [loading, setLoading] = useState(true);

  const API = process.env.REACT_APP_BACKEND_URL;
  const token = localStorage.getItem("token");

  useEffect(() => {
    const fetchAdminAndHero = async () => {
      try {
        if (!token) {
          navigate("/login");
          return;
        }
        const me = await axios.get(`${API}/api/auth/me`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        if (me.data.user.role !== "admin") {
          alert("Access denied  Admins only");
          navigate("/");
          return;
        }

        setUser(me.data.user);
        const res = await axios.get(`${API}/api/hero`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        const heroData = res.data[0] || {};
        setHero(heroData);
        setFormData(heroData);
      } catch (err) {
        console.error("Failed to fetch admin/hero data:", err);
        localStorage.removeItem("token");
        navigate("/login");
      } finally {
        setLoading(false);
      }
    };

    fetchAdminAndHero();
  }, [navigate, token, API]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSave = async () => {
    try {
      const response = await axios.put(`${API}/api/hero`, formData, {
        headers: { Authorization: `Bearer ${token}` },
      });

      if (response.data.success) {
        setHero(formData);
        setEditing(false);
        alert("Hero section updated successfully!");
      } else {
        alert("Failed to update hero section");
      }
    } catch (err) {
      console.error(err);
      alert("Something went wrong while updating hero section");
    }
  };

  if (loading) return <div className="admin-hero-container">Loading...</div>;

  return (
    <div className="admin-hero-container">
      <h1>Edit Hero Section</h1>

      <div className="hero-form">
        <label>
          Title:
          <input
            type="text"
            name="title"
            value={formData.title || ""}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <label>
          Subtitle:
          <input
            type="text"
            name="subtitle"
            value={formData.subtitle || ""}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <label>
          CTA Primary:
          <input
            type="text"
            name="ctaPrimary"
            value={formData.ctaPrimary || ""}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <label>
          CTA Secondary:
          <input
            type="text"
            name="ctaSecondary"
            value={formData.ctaSecondary || ""}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <div className="actions">
          {editing ? (
            <>
              <button className="save-btn" onClick={handleSave}>Save</button>
              <button className="cancel-btn" onClick={() => setEditing(false)}>Cancel</button>
            </>
          ) : (
            <button className="edit-btn" onClick={() => setEditing(true)}>Edit</button>
          )}
        </div>
      </div>

      <button className="back-btn" onClick={() => navigate("/admin")}>Back to Admin Panel</button>
    </div>
  );
};

export default AdminHero;
