import React, { useEffect, useState } from "react";
import "../Styles/AdminUsers.css";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const AdminUsers = () => {
  const navigate = useNavigate();
  const [user, setUser] = useState(null);
  const [users, setUsers] = useState([]);
  const [filteredUsers, setFilteredUsers] = useState([]);
  const [editingUser, setEditingUser] = useState(null);
  const [searchQuery, setSearchQuery] = useState("");
  const [formData, setFormData] = useState({
    full_name: "",
    username: "",
    email: "",
    password: "",
    role: "",
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  const API = process.env.REACT_APP_BACKEND_URL;
  const token = localStorage.getItem("token");

  useEffect(() => {
    const fetchAdminAndUsers = async () => {
      try {
        if (!token) {
          navigate("/login");
          return;
        }
        const me = await axios.get(`${API}/api/auth/me`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        if (me.data.user.role !== "admin") {
          alert("Access denied  Admins only");
          navigate("/");
          return;
        }
        setUser(me.data.user);
        const res = await axios.get(`${API}/api/users`, {
          headers: { Authorization: `Bearer ${token}` },
        });
        setUsers(res.data);
        setFilteredUsers(res.data);
      } catch (err) {
        console.error(err);
        localStorage.removeItem("token");
        navigate("/login");
      } finally {
        setLoading(false);
      }
    };

    fetchAdminAndUsers();
  }, [navigate, token, API]);

  useEffect(() => {
    const query = searchQuery.toLowerCase();
    setFilteredUsers(
      users.filter(
        (u) =>
          u.username.toLowerCase().includes(query) ||
          u.full_name.toLowerCase().includes(query) ||
          u.email.toLowerCase().includes(query) ||
          u.role.toLowerCase().includes(query)
      )
    );
  }, [searchQuery, users]);

  const handleEdit = (user) => {
    setEditingUser(user);
    setFormData({
      full_name: user.full_name,
      username: user.username,
      email: user.email,
      password: "",
      role: user.role,
    });
  };

  const handleSave = async () => {
    try {
      await axios.put(
        `${API}/api/users/${editingUser.user_id}`,
        formData,
        { headers: { Authorization: `Bearer ${token}` } }
      );

      setUsers((prev) =>
        prev.map((u) =>
          u.user_id === editingUser.user_id
            ? { ...u, ...formData, password: undefined }
            : u
        )
      );
      setEditingUser(null);
    } catch (err) {
      console.error(err);
      alert("Failed to update user.");
    }
  };

  const handleBanToggle = async (user_id) => {
    if (!window.confirm("Are you sure you want to toggle ban for this user?")) return;
    try {
      await axios.put(
        `${API}/api/users/${user_id}/ban`,
        {},
        { headers: { Authorization: `Bearer ${token}` } }
      );
      setUsers((prev) =>
        prev.map((u) =>
          u.user_id === user_id
            ? { ...u, status: u.status === "banned" ? "active" : "banned" }
            : u
        )
      );
    } catch (err) {
      console.error(err);
      alert("Failed to toggle ban.");
    }
  };

  if (loading) return <div className="admin-users-container">Loading users...</div>;
  if (error) return <div className="admin-users-container error">{error}</div>;

  return (
    <div className="admin-users-container">
      <h1>Manage Users</h1>
      <p className="admin-description">Add, edit, or ban user accounts.</p>

      <input
        type="text"
        className="search-input"
        placeholder="Search by name, username, email, or role..."
        value={searchQuery}
        onChange={(e) => setSearchQuery(e.target.value)}
      />

      <div className="table-wrapper">
        <table className="admin-users-table">
          <thead>
            <tr>
              <th>ID</th>
              <th>Full Name</th>
              <th>Username</th>
              <th>Email</th>
              <th>Role</th>
              <th>Status</th>
              <th>Created</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {filteredUsers.map((u) => (
              <tr key={u.user_id} className={u.status === "banned" ? "banned" : ""}>
                <td>{u.user_id}</td>
                <td>{u.full_name}</td>
                <td>{u.username}</td>
                <td>{u.email}</td>
                <td>{u.role}</td>
                <td>
                  <span className={`status-badge ${u.status === "banned" ? "banned" : "active"}`}>
                    {u.status || "Unknown"}
                  </span>
                </td>
                <td>{new Date(u.created_at).toLocaleDateString()}</td>
                <td>
                  <button className="admin-btn edit" onClick={() => handleEdit(u)}>Edit</button>
                  <button
                    className={`admin-btn ban ${u.status === "banned" ? "unban" : ""}`}
                    onClick={() => handleBanToggle(u.user_id)}
                  >
                    {u.status === "banned" ? "Unban" : "Ban"}
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {editingUser && (
        <div className="edit-modal">
          <div className="modal-content">
            <h3>Edit User #{editingUser.user_id}</h3>
            <input
              type="text"
              value={formData.full_name}
              onChange={(e) => setFormData({ ...formData, full_name: e.target.value })}
              placeholder="Full Name"
            />
            <input
              type="text"
              value={formData.username}
              onChange={(e) => setFormData({ ...formData, username: e.target.value })}
              placeholder="Username"
            />
            <input
              type="email"
              value={formData.email}
              onChange={(e) => setFormData({ ...formData, email: e.target.value })}
              placeholder="Email"
            />
            <input
              type="password"
              value={formData.password}
              onChange={(e) => setFormData({ ...formData, password: e.target.value })}
              placeholder="Password (optional)"
            />
            <label>Role:</label>
            <select
              value={formData.role}
              onChange={(e) => setFormData({ ...formData, role: e.target.value })}
            >
              <option value="user">User</option>
              <option value="admin">Admin</option>
            </select>

            <div className="edit-actions">
              <button className="save" onClick={handleSave}>Save</button>
              <button className="cancel" onClick={() => setEditingUser(null)}>Cancel</button>
            </div>
          </div>
        </div>
      )}

      <button className="back-btn" onClick={() => navigate("/admin")}>Back to Admin Panel</button>
      <div className="admin-edit-decoration"></div>
    </div>
  );
};

export default AdminUsers;
