import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "../Styles/AdminStats.css";

const AdminStats = () => {
  const navigate = useNavigate();
  const [user, setUser] = useState(null);
  const [stats, setStats] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [formData, setFormData] = useState({ label: "", value: "" });
  const [editingId, setEditingId] = useState(null);

  const API = process.env.REACT_APP_BACKEND_URL;
  const token = localStorage.getItem("token");

  useEffect(() => {
    const fetchAdminAndStats = async () => {
      try {
        if (!token) {
          navigate("/login");
          return;
        }
        const me = await axios.get(`${API}/api/auth/me`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        if (me.data.user.role !== "admin") {
          alert("Access denied  Admins only");
          navigate("/");
          return;
        }

        setUser(me.data.user);
        const res = await axios.get(`${API}/api/getstats`, {
          headers: { Authorization: `Bearer ${token}` },
        });
        setStats(res.data.rows || []);
      } catch (err) {
        console.error("Failed to fetch admin/stats:", err);
        localStorage.removeItem("token");
        navigate("/login");
      } finally {
        setLoading(false);
      }
    };

    fetchAdminAndStats();
  }, [navigate, token, API]);

  const handleChange = (e) => {
    setFormData((prev) => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleAdd = async () => {
    if (!formData.label || !formData.value) return alert("Fill all fields");
    try {
      const res = await axios.post(`${API}/api/getstats`, formData, {
        headers: { Authorization: `Bearer ${token}` },
      });
      if (res.data.success) {
        setStats([{ id: res.data.id || Date.now(), ...formData }, ...stats]);
        setFormData({ label: "", value: "" });
      } else alert(res.data.message);
    } catch (err) {
      console.error(err);
      alert("Failed to add stat");
    }
  };

  const handleEdit = (stat) => {
    setEditingId(stat.id);
    setFormData({ label: stat.label, value: stat.value });
  };

  const handleSave = async () => {
    if (!formData.label || !formData.value) return alert("Fill all fields");
    try {
      const res = await axios.put(`${API}/api/getstats/${editingId}`, formData, {
        headers: { Authorization: `Bearer ${token}` },
      });
      if (res.data.success) {
        setStats(stats.map((s) => (s.id === editingId ? { ...s, ...formData } : s)));
        setEditingId(null);
        setFormData({ label: "", value: "" });
      } else alert(res.data.message);
    } catch (err) {
      console.error(err);
      alert("Failed to update stat");
    }
  };

  const handleDelete = async (id) => {
    if (!window.confirm("Are you sure you want to delete this stat?")) return;
    try {
      const res = await axios.delete(`${API}/api/getstats/${id}`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      if (res.data.success) setStats(stats.filter((s) => s.id !== id));
      else alert(res.data.message);
    } catch (err) {
      console.error(err);
      alert("Failed to delete stat");
    }
  };

  if (loading) return <div className="admin-stats-container">Loading...</div>;
  if (error) return <div className="admin-stats-container error">{error}</div>;

  return (
    <div className="admin-stats-container">
      <h1>Manage Stats</h1>

      <div className="stats-form">
        <label>
          Label:
          <input type="text" name="label" value={formData.label} onChange={handleChange} />
        </label>

        <label>
          Value:
          <input type="text" name="value" value={formData.value} onChange={handleChange} />
        </label>

        <div className="actions">
          {editingId ? (
            <>
              <button className="save-btn" onClick={handleSave}>Save</button>
              <button
                className="cancel-btn"
                onClick={() => {
                  setEditingId(null);
                  setFormData({ label: "", value: "" });
                }}
              >
                Cancel
              </button>
            </>
          ) : (
            <button className="add-btn" onClick={handleAdd}>Add Stat</button>
          )}
        </div>
      </div>

      <div className="stats-list">
        {stats.map((stat) => (
          <div key={stat.id} className="stat-card">
            <div>
              <h3>{stat.value}</h3>
              <p>{stat.label}</p>
            </div>
            <div className="card-actions">
              <button className="edit-btn" onClick={() => handleEdit(stat)}>Edit</button>
              <button className="delete-btn" onClick={() => handleDelete(stat.id)}>Delete</button>
            </div>
          </div>
        ))}
      </div>

      <button className="back-btn" onClick={() => navigate("/admin")}>Back to Admin Panel</button>
    </div>
  );
};

export default AdminStats;
