import React, { useEffect, useState } from "react";
import "../Styles/AdminContact.css";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const AdminContact = () => {
  const navigate = useNavigate();
  const [user, setUser] = useState(null);
  const [contact, setContact] = useState(null);
  const [loading, setLoading] = useState(true);
  const [editing, setEditing] = useState(false);
  const [formData, setFormData] = useState({
    id: null,
    title: "",
    email: "",
    phone: "",
    address: "",
  });

  useEffect(() => {
    const fetchAdminAndContact = async () => {
      try {
        const token = localStorage.getItem("token");
        if (!token) {
          navigate("/login");
          return;
        }
        const me = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/api/auth/me`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        if (me.data.user.role !== "admin") {
          alert("Access denied  Admins only");
          navigate("/");
          return;
        }

        setUser(me.data.user);
        const res = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/api/contact`, {
          headers: { Authorization: `Bearer ${token}` },
        });

        if (res.data) {
          setContact(res.data);
          setFormData({
            id: res.data.id || null,
            title: res.data.title || "",
            email: res.data.email || "",
            phone: res.data.phone || "",
            address: res.data.address || "",
          });
        }
      } catch (err) {
        console.error("Error fetching contact info:", err);
        localStorage.removeItem("token");
        navigate("/login");
      } finally {
        setLoading(false);
      }
    };

    fetchAdminAndContact();
  }, [navigate]);

  const handleChange = (e) => {
    setFormData((prev) => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleSave = async () => {
    if (!contact?.id) {
      alert("Contact ID is missing. Cannot save changes.");
      return;
    }

    try {
      const token = localStorage.getItem("token");
      await axios.put(
        `${process.env.REACT_APP_BACKEND_URL}/api/contact/${contact.id}`,
        formData,
        { headers: { Authorization: `Bearer ${token}` } }
      );

      setContact({ ...contact, ...formData });
      setEditing(false);
      alert("contact info updated successfully!");
    } catch (err) {
      console.error("Failed to update contact info:", err);
      alert("Failed to update contact info.");
    }
  };

  if (loading) return <div className="admin-contact-container">Loading...</div>;

  return (
    <div className="admin-contact-container">
      <h1>Edit Contact Information</h1>

      <div className="contact-card">
        <label>
          Title:
          <input
            type="text"
            name="title"
            value={formData.title}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <label>
          Email:
          <input
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <label>
          Phone:
          <input
            type="text"
            name="phone"
            value={formData.phone}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <label>
          Address:
          <input
            type="text"
            name="address"
            value={formData.address}
            onChange={handleChange}
            disabled={!editing}
          />
        </label>

        <div className="actions">
          {editing ? (
            <>
              <button className="save-btn" onClick={handleSave}>Save</button>
              <button className="cancel-btn" onClick={() => setEditing(false)}>Cancel</button>
            </>
          ) : (
            <button className="edit-btn" onClick={() => setEditing(true)}>Edit</button>
          )}
        </div>
      </div>

      <button className="back-btn" onClick={() => navigate("/admin")}>
        Back to Admin Panel
      </button>
      <div className="admin-edit-decoration"></div>
    </div>
  );
};

export default AdminContact;
