import React, { useEffect, useState } from "react";
import "../Styles/AdminAbout.css";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const AdminAbout = () => {
  const navigate = useNavigate();
  const [about, setAbout] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [formData, setFormData] = useState({
    hero_title: "",
    hero_description: "",
    mission_title: "",
    mission_text: "",
    cta_title: "",
    cta_text: "",
    cta_link: "",
  });
  const [editing, setEditing] = useState(false);

  useEffect(() => {
    const checkAdminAndFetch = async () => {
      try {
        const token = localStorage.getItem("token");
        if (!token) {
          navigate("/");
          return;
        }

        const me = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/auth/me`,
          {
            headers: { Authorization: `Bearer ${token}` },
          }
        );

        if (me.data.user.role !== "admin") {
          navigate("/");
          return;
        }

        const res = await axios.get(`${process.env.REACT_APP_BACKEND_URL}/api/about`);
        setAbout(res.data);
        setFormData(res.data);
      } catch (err) {
        console.error(err);
        navigate("/");
      } finally {
        setLoading(false);
      }
    };

    checkAdminAndFetch();
  }, [navigate]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSave = async () => {
    try {
      const token = localStorage.getItem("token");
      const response = await axios.put(
        `${process.env.REACT_APP_BACKEND_URL}/api/about`,
        formData,
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      );

      if (response.data.success) {
        setAbout({ ...about, ...formData });
        setEditing(false);
        alert(" About page updated successfully!");
      } else {
        alert(response.data.message || " Failed to update about page.");
      }
    } catch (err) {
      console.error(err);
      alert(" Something went wrong while updating the About page.");
    }
  };

  if (loading) return <div className="admin-about-container">Loading...</div>;
  if (error) return <div className="admin-about-container error">{error}</div>;

  return (
    <div className="admin-about-container">
      <h1>Edit About Page</h1>

      <div className="about-form">
        <label>
          Hero Title:
          <input
            type="text"
            name="hero_title"
            value={formData.hero_title || ""}
            disabled={!editing}
            onChange={handleChange}
          />
        </label>

        <label>
          Hero Description:
          <textarea
            name="hero_description"
            value={formData.hero_description || ""}
            disabled={!editing}
            onChange={handleChange}
          />
        </label>

        <label>
          Mission Title:
          <input
            type="text"
            name="mission_title"
            value={formData.mission_title || ""}
            disabled={!editing}
            onChange={handleChange}
          />
        </label>

        <label>
          Mission Text:
          <textarea
            name="mission_text"
            value={formData.mission_text || ""}
            disabled={!editing}
            onChange={handleChange}
          />
        </label>

        <label>
          CTA Title:
          <input
            type="text"
            name="cta_title"
            value={formData.cta_title || ""}
            disabled={!editing}
            onChange={handleChange}
          />
        </label>

        <label>
          CTA Text:
          <textarea
            name="cta_text"
            value={formData.cta_text || ""}
            disabled={!editing}
            onChange={handleChange}
          />
        </label>

        <label>
          CTA Link:
          <input
            type="text"
            name="cta_link"
            value={formData.cta_link || ""}
            disabled={!editing}
            onChange={handleChange}
          />
        </label>

        <div className="actions">
          {editing ? (
            <>
              <button className="save-btn" onClick={handleSave}>
                Save
              </button>
              <button className="cancel-btn" onClick={() => setEditing(false)}>
                Cancel
              </button>
            </>
          ) : (
            <button className="edit-btn" onClick={() => setEditing(true)}>
              Edit
            </button>
          )}
        </div>
      </div>

      <button className="back-btn" onClick={() => navigate("/admin")}>
        Back to Admin Panel
      </button>

      <div className="admin-edit-decoration"></div>
    </div>
  );
};

export default AdminAbout;
