import React, { useEffect, useState } from "react";
import "../Styles/Profile.css";

const ProfilePage = ({ user, setUser }) => {
  const [fullName, setFullName] = useState("");
  const [username, setUsername] = useState("");
  const [currentPassword, setCurrentPassword] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [currentEmail, setCurrentEmail] = useState("");
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState({ text: "", type: "" });

  useEffect(() => {
    async function fetchProfile() {
      try {
        const res = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/profile`, { credentials: "include" });
        const data = await res.json();
        if (data.success) {
          setFullName(data.user.full_name || "");
          setUsername(data.user.username || "");
          setCurrentEmail(data.user.email || "");
        }
      } catch (err) {
        console.error(err);
        setMessage({ text: "Failed to load profile.", type: "error" });
      }
    }
    fetchProfile();
  }, []);

  const handleUpdateProfile = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage({ text: "", type: "" });

    try {
      const res = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/profile`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify({ full_name: fullName, username }),
      });
      const data = await res.json();
      setMessage({ text: data.message || "Profile updated!", type: data.success ? "success" : "error" });
      if (data.success && setUser) setUser({ ...user, full_name: fullName, username });
    } catch (err) {
      setMessage({ text: "Failed to update profile.", type: "error" });
    } finally {
      setLoading(false);
    }
  };

  const handleChangePassword = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage({ text: "", type: "" });

    try {
      const res = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/profile/password`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify({ currentPassword, newPassword }),
      });
      const data = await res.json();
      setMessage({ text: data.message || "Password updated!", type: data.success ? "success" : "error" });
    } catch (err) {
      setMessage({ text: "Failed to change password.", type: "error" });
    } finally {
      setLoading(false);
      setCurrentPassword("");
      setNewPassword("");
    }
  };

  const handleEmailChange = async (e) => {
    e.preventDefault();
    setLoading(true);
    setMessage({ text: "", type: "" });

    try {
      const res = await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/profile/email`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify({ email: currentEmail }),
      });
      const data = await res.json();
      setMessage({ text: data.message || "Email updated!", type: data.success ? "success" : "error" });
    } catch (err) {
      setMessage({ text: "Failed to update email.", type: "error" });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="profile-container modern">
      <h2 className="profile-title">My Profile</h2>

      {message.text && (
        <div className={`profile-message ${message.type}`}>
          {message.text}
        </div>
      )}

      <div className="profile-grid">
        <div className="profile-card">
          <h3>Update Info</h3>
          <form onSubmit={handleUpdateProfile} className="profile-form">
            <label>Full Name</label>
            <input
              type="text"
              value={fullName}
              onChange={(e) => setFullName(e.target.value)}
              placeholder="Enter your full name"
              required
            />

            <label>Username</label>
            <input
              type="text"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              placeholder="Enter your username"
              required
            />

            <button type="submit" className="btn-primary" disabled={loading}>
              {loading ? "Saving..." : "Update Profile"}
            </button>
          </form>
        </div>

        <div className="profile-card">
          <h3>Change Password</h3>
          <form onSubmit={handleChangePassword} className="profile-form">
            <label>Current Password</label>
            <input
              type="password"
              value={currentPassword}
              onChange={(e) => setCurrentPassword(e.target.value)}
              placeholder="Enter current password"
              required
            />

            <label>New Password</label>
            <input
              type="password"
              value={newPassword}
              onChange={(e) => setNewPassword(e.target.value)}
              placeholder="Enter new password"
              required
            />

            <button type="submit" className="btn-primary" disabled={loading}>
              {loading ? "Saving..." : "Change Password"}
            </button>
          </form>
        </div>

        <div className="profile-card">
          <h3>Change Email</h3>
          <form onSubmit={handleEmailChange} className="profile-form">
            <label>Email</label>
            <input
              type="email"
              value={currentEmail}
              onChange={(e) => setCurrentEmail(e.target.value)}
              placeholder="Enter new email"
              required
            />

            <button type="submit" className="btn-primary" disabled={loading}>
              {loading ? "Saving..." : "Change Email"}
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default ProfilePage;
