import React, { useState, useMemo, useEffect } from "react";
import { PieChart, Pie, Cell, ResponsiveContainer, Tooltip } from "recharts";
import { useNavigate } from "react-router-dom";
import "../Styles/Dashboard.css";

const Dashboard = () => {
  const navigate = useNavigate();

  const [userId, setUserId] = useState(null);
  const [transactions, setTransactions] = useState([]);
  const [budget, setBudget] = useState(0);
  const [filterText, setFilterText] = useState("");
  const [loading, setLoading] = useState(true);

  const [form, setForm] = useState({
    date: "",
    desc: "",
    amount: "",
    category: "",
  });

  const monthYear = new Date().toISOString().slice(0, 7);
  const COLORS = ["#0d6efd", "#198754", "#ffc107", "#dc3545", "#6f42c1"];

  const currency = (n) =>
    `${n < 0 ? "-" : ""}$${Math.abs(n).toFixed(2)}`;

  const token = localStorage.getItem("token");

  useEffect(() => {
    if (!token) {
      navigate("/login");
      return;
    }

    const fetchUser = async () => {
      try {
        const res = await fetch(
          `${process.env.REACT_APP_BACKEND_URL}/api/auth/me`,
          {
            headers: { Authorization: `Bearer ${token}` },
          }
        );

        const data = await res.json();
        if (!data.success) throw new Error();
        setUserId(data.user.id);
      } catch {
        localStorage.removeItem("token");
        navigate("/login");
      }
    };

    fetchUser();
  }, [navigate, token]);

  useEffect(() => {
    if (!userId) return;

    const fetchData = async () => {
      try {
        const [txRes, budgetRes] = await Promise.all([
          fetch(
            `${process.env.REACT_APP_BACKEND_URL}/api/transactions/${userId}`,
            { headers: { Authorization: `Bearer ${token}` } }
          ),
          fetch(
            `${process.env.REACT_APP_BACKEND_URL}/api/budget/${userId}/${monthYear}`,
            { headers: { Authorization: `Bearer ${token}` } }
          ),
        ]);

        const txData = await txRes.json();
        const budgetData = await budgetRes.json();
        console.log(txData.transactions);
        setTransactions(
          txData.success
            ? txData.transactions.map((t) => ({
              ...t,
              amount: Number(t.amount),
            }))
            : []
        );

        setBudget(
          budgetData.success && budgetData.budget
            ? budgetData.budget.amount
            : 0
        );
      } catch {
        alert("Failed to load data");
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [userId, monthYear, token]);

  const totals = useMemo(() => {
    let spent = 0,
      income = 0;

    transactions.forEach((t) => {
      if (t.amount < 0) spent += -t.amount;
      else income += t.amount;
    });

    return { spent, income };
  }, [transactions]);

  const categoryBreakdown = useMemo(() => {
    const map = {};
    transactions.forEach((t) => {
      if (t.amount < 0) {
        const cat = t.category || "Other";
        map[cat] = (map[cat] || 0) + -t.amount;
      }
    });

    return Object.entries(map).map(([name, value]) => ({ name, value }));
  }, [transactions]);

  const addTransaction = async (e) => {
    e.preventDefault();
    const amt = Number(form.amount);

    if (!form.date || !form.desc || isNaN(amt)) {
      alert("Fill the form correctly");
      return;
    }

    try {
      const res = await fetch(
        `${process.env.REACT_APP_BACKEND_URL}/api/transactions`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify({
            userId,
            date: form.date,
            description: form.desc,
            amount: amt,
            category: form.category,
          }),
        }
      );

      const data = await res.json();
      if (!data.success) throw new Error();

      setTransactions((prev) => [
        { ...data.transaction, amount: Number(data.transaction.amount) },
        ...prev,
      ]);

      setForm({ date: "", desc: "", amount: "", category: "" });
    } catch {
      alert("Failed to add transaction");
    }
  };


  const updateBudget = async (newBudget) => {
    setBudget(newBudget);

    if (!userId) return;

    try {
      await fetch(`${process.env.REACT_APP_BACKEND_URL}/api/budget`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({
          userId,
          monthYear,
          amount: newBudget,
        }),
      });
    } catch (err) {
      console.error("Update budget failed:", err);
    }
  };


  const removeTransaction = async (id) => {
    try {
      const res = await fetch(
        `${process.env.REACT_APP_BACKEND_URL}/api/transactions/${id}`,
        {
          method: "DELETE",
          headers: { Authorization: `Bearer ${token}` },
        }
      );

      const data = await res.json();
      if (data.success)
        setTransactions((prev) => prev.filter((t) => t.id !== id));
    } catch {
      alert("Failed to remove transaction");
    }
  };

  const filtered = transactions.filter((t) =>
    `${t.description} ${t.category} ${t.date}`
      .toLowerCase()
      .includes(filterText.toLowerCase())
  );

  const progress = budget
    ? Math.min(100, Math.round((totals.spent / budget) * 100))
    : 0;

  if (loading) return <div className="loading">Loading...</div>;

  return (
    <div className="dashboard modern-dashboard">
      <header className="dashboard-header">
        <div className="header-text">
          <h1>BudgetTrack</h1>
          <p>Track your spending and control your monthly budget effortlessly.</p>
        </div>
        <div className="budget-box">
          <label>Monthly Budget</label>
          <input type="number" value={budget} onChange={(e) => updateBudget(Number(e.target.value || 0))} />
          <div className="budget-display">{currency(budget)}</div>
        </div>
      </header>

      <div className="dashboard-grid">
        <div className="card overview-card">
          <h3>Overview</h3>
          <div className="overview">
            <div>
              <span>Spent</span>
              <strong className="text-red">{currency(totals.spent)}</strong>
            </div>
            <div>
              <span>Income</span>
              <strong className="text-green">{currency(totals.income)}</strong>
            </div>
            <div>
              <span>Remaining</span>
              <strong>{currency(budget - totals.spent)}</strong>
            </div>
          </div>
          <div className="progress-bar">
            <div style={{ width: `${progress}%` }}></div>
          </div>
          <p className="text-muted">{progress}% used</p>
        </div>

        <div className="card category-card">
          <h3>Spending by Category</h3>
          {categoryBreakdown.length ? (
            <ResponsiveContainer width="100%" height={200}>
              <PieChart>
                <Pie data={categoryBreakdown} dataKey="value" nameKey="name" outerRadius={80} label>
                  {categoryBreakdown.map((_, i) => (
                    <Cell key={i} fill={COLORS[i % COLORS.length]} />
                  ))}
                </Pie>
                <Tooltip />
              </PieChart>
            </ResponsiveContainer>
          ) : (
            <p className="no-expenses">No expenses yet</p>
          )}
          <ul className="category-list">
            {categoryBreakdown.map((c, i) => (
              <li key={i}>
                <span>
                  <span className="dot" style={{ background: COLORS[i % COLORS.length] }}></span>
                  {c.name}
                </span>
                <span>{currency(c.value)}</span>
              </li>
            ))}
          </ul>
        </div>
      </div>

      <div className="dashboard-grid">
        <div className="card quick-add-card">
          <h3>Quick Add Transaction</h3>
          <form onSubmit={addTransaction} className="quick-add-form">
            <input type="date" value={form.date} onChange={(e) => setForm({ ...form, date: e.target.value })} required />
            <input type="text" placeholder="Description" value={form.desc} onChange={(e) => setForm({ ...form, desc: e.target.value })} required />
            <input type="number" placeholder="Amount" value={form.amount} onChange={(e) => setForm({ ...form, amount: e.target.value })} required />
            <input type="text" placeholder="Category" value={form.category} onChange={(e) => setForm({ ...form, category: e.target.value })} />
            <div className="form-actions">
              <button className="btn primary">Add</button>
              <button type="button" className="btn secondary" onClick={() => setForm({ date: "", desc: "", amount: "", category: "" })}>Reset</button>
            </div>
          </form>
        </div>

        <div className="card transactions-card">
          <div className="transactions-header">
            <h3>Transactions</h3>
            <div className="search-box">
              <input type="text" placeholder="Search..." value={filterText} onChange={(e) => setFilterText(e.target.value)} />
              <button onClick={() => setFilterText("")}></button>
            </div>
          </div>
          <div className="table-wrapper">
            <table className="transactions">
              <thead>
                <tr>
                  <th>Date</th>
                  <th>Description</th>
                  <th>Category</th>
                  <th>Amount</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {filtered.map((t) => (
                  <tr key={t.id}>
                    <td>{t.date}</td>
                    <td>{t.description}</td>
                    <td>{t.category}</td>
                    <td className={t.amount < 0 ? "text-red" : "text-green"}>{currency(t.amount)}</td>
                    <td>
                      <button className="btn danger small" onClick={() => removeTransaction(t.id)}>Remove</button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div className="table-footer">
            <span>Showing {filtered.length} transactions</span>
            <span>Total: {currency(filtered.reduce((s, t) => s + t.amount, 0))}</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
